// src/App.js
import React, { useState } from 'react';
import UploadForm from './components/UploadForm';
import TranscriptionDisplay from './components/TranscriptionDisplay';
import { styled } from '@mui/system';
import { CssBaseline, Container, AppBar, Toolbar, Typography } from '@mui/material';

const AppContainer = styled('div')({
    textAlign: 'center',
    fontFamily: 'Arial, sans-serif',
    backgroundColor: '#f5f5f5',
    color: '#333',
    minHeight: '100vh',
});

const MainContent = styled('div')({
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    marginTop: '80px', // To ensure there's some space below the fixed AppBar
});

const App = () => {
    const [transcription, setTranscription] = useState(null);

    return (
        <AppContainer>
            <CssBaseline />
            <AppBar position="fixed">
                <Toolbar>
                    <Typography variant="h6">Audio Transcription</Typography>
                </Toolbar>
            </AppBar>
            <MainContent>
                <Container>
                    <UploadForm setTranscription={setTranscription} />
                    <TranscriptionDisplay transcription={transcription} />
                </Container>
            </MainContent>
        </AppContainer>
    );
};

export default App;
