// src/components/TranscriptionDisplay.js
import React from 'react';
import { styled } from '@mui/system';
import { IconButton, Tooltip } from '@mui/material';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import { CopyToClipboard } from 'react-copy-to-clipboard';

const DisplayContainer = styled('div')({
    marginTop: '20px',
    textAlign: 'left',
    maxWidth: '800px',
    margin: '0 auto',
});

const Segment = styled('div')({
    marginBottom: '20px',
    padding: '10px',
    border: '1px solid #ccc',
    borderRadius: '5px',
    backgroundColor: '#f9f9f9',
    position: 'relative'
});

const Word = styled('span')({
    marginRight: '5px',
    padding: '2px 4px',
    backgroundColor: '#e0e0e0',
    borderRadius: '3px',
    cursor: 'default',
    '&:hover': {
        backgroundColor: '#d0d0d0',
    },
});

const TranscriptionDisplay = ({ transcription }) => {
    if (!transcription) return null;

    return (
        <DisplayContainer>
            {transcription.map((segment, index) => (
                <Segment key={index}>
                    <p>
                        <strong>{segment.speaker}</strong> ({segment.start.toFixed(2)} - {segment.end.toFixed(2)}): {segment.text}
                        <CopyToClipboard text={segment.text}>
                            <Tooltip title="Copy to clipboard">
                                <IconButton size="small" style={{ marginLeft: '10px' }}>
                                    <ContentCopyIcon fontSize="small" />
                                </IconButton>
                            </Tooltip>
                        </CopyToClipboard>
                    </p>
                    <div>
                        {segment.words.map((word, idx) => (
                            <Word key={idx} title={`Confidence: ${word.score}`}>
                                {word.word}{' '}
                            </Word>
                        ))}
                    </div>
                </Segment>
            ))}
        </DisplayContainer>
    );
};

export default TranscriptionDisplay;
