import React, { useState } from 'react';
import axios from 'axios';
import { Button, CircularProgress, TextField } from '@mui/material';
import { styled } from '@mui/system';

const FormContainer = styled('div')({
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    marginTop: '20px',
});

const FileInput = styled(TextField)({
    marginBottom: '20px',
});

const UploadButton = styled(Button)({
    marginTop: '10px',
    marginLeft:'20px'
});

const UploadForm = ({ setTranscription }) => {
    const [file, setFile] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    const handleFileChange = (e) => {
        setFile(e.target.files[0]);
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError(null);
        if (!file) return;

        setLoading(true);
        const formData = new FormData();
        formData.append('audio', file);

        try {
            const response = await axios.post('http://142.112.39.215:50620/transcribe', formData, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            });
            setTranscription(response.data);
        } catch (error) {
            console.error('Error uploading file:', error);
            setError(error.response ? error.response.data : 'An error occurred');
        } finally {
            setLoading(false);
        }
    };

    return (
        <FormContainer>
            <form onSubmit={handleSubmit}>
                <FileInput
                    type="file"
                    onChange={handleFileChange}
                    inputProps={{ accept: 'audio/*' }}
                    variant="outlined"
                />
                <UploadButton
                    type="submit"
                    variant="contained"
                    color="primary"
                    disabled={!file || loading}
                >
                    {loading ? <CircularProgress size={24} /> : 'Upload'}
                </UploadButton>
            </form>
            {error && <div className="error" style={{ color: 'red', marginTop: '20px' }}>{error}</div>}
        </FormContainer>
    );
};

export default UploadForm;
